  signupor: "Or",
  privacypolicy1: "1. Application Privacy Policy",
  privacypolicy2: "VCA Perú operates the Conversation Management Platform Laraigo",
  privacypolicy3: "In this page, we inform you of our policies regarding the collection, use and disclosure of personal information we receive from users of the site.",
  privacypolicy4: "We use your personal information only for providing and improving the platform. By using Laraigo, you agree to the collection and use of information in accordance with this policy.",
  privacypolicy5: "2. Information Collection and Use",
  privacypolicy6: "2.1. Personal Information:",
  privacypolicy7: "While using Laraigo, we may ask you to provide us with certain personally identifiable information that can be used to contact or identify you. Personal identifiable information includes e-mail, name, last name, country, phone number and your current role in your company, all of which are asked as part of our sign-up process.",
  privacypolicy8: "We use this information as part of our user creation process. The user created in sign-up can be identified by the data provided and can be used to contact the person that registered it.",
  privacypolicy9: "2.2. Business Information:",
  privacypolicy10: "While signing-up to Laraigo, we ask you to provide the following information about your business: Business or company name, industry field and business or company size.",
  privacypolicy11: "We use this information as part of our user creation process. Each user created in our sign-up process is linked to a corporation that contains the data provided. We use the information in order to identify the user and contact him if needed.",
  privacypolicy12: "2.3. Facebook Information:",
  privacypolicy13: "In Laraigo, we offer the possibility to link your Facebook account to one of our apps on multiple occasions. In the following paragraphs we detail the information that is shared on each possible link and the way we use it.",
  privacypolicy14: "2.3.1. Sign-up:",
  privacypolicy15: "We offer the possibility to use your Facebook account in our sign-up process. If you agree to link your Facebook account as a mean to sign-up we receive the name of your account, e-mail and avatar picture.",
  privacypolicy16: "We use the information provided as part of our user creation process. The user created by signing-up with a Facebook account can be identified by the data provided and can be used to contact the person that registered it if needed.",
  privacypolicy17: "2.3.2. Channel Management:",
  privacypolicy18: "We offer the possibility to link Facebook pages to our apps in our channel management module. In the linking process, we receive the following information of the page: The external ID page and the name. In addition, by linking a Facebook page to our platform, you agree to share with us the following event-based information:",
  privacypolicy19: "Posts created by the page",
  privacypolicy20: "Comments created by the page or other users in posts of the page",
  privacypolicy21: "Messages sent or received by the Messenger of the page",
  privacypolicy22: "Basic profile information of the users that interacted with the page (Name and avatar picture)",
  privacypolicy23: "We use the information provided as part of our channel management module. The information of the page is used to create a channel where the incoming events (Posts, comments and messages) are registered in our platform and processed as tickets. Some select users of the platform can then send replies to these tickets which are then shown as replies in Facebook.",
  privacypolicy24: "2.4. Instagram Information:",
  privacypolicy25: "In Laraigo, we offer the possibility to link your Instagram account to one of our apps.",
  privacypolicy26: "2.4.1. Channel Management:",
  privacypolicy27: "We offer the possibility to link Instagram pages to our apps in our channel management module. In the linking process, we receive the following information of the page: The external ID page and the name. We also receive information about the linked Instagram Business account, including the id. In addition, by linking an Instagram page to our platform, you agree to share with us the following event-based information:",
  privacypolicy28: "Comments created by the page or other users in posts of the page",
  privacypolicy29: "Messages sent or received by the Messenger of the page",
  privacypolicy30: "Basic profile information of the users that interacted with the page (Name and avatar picture)",
  privacypolicy31: "We use the information provided as part of our channel management module. The information of the page is used to create a channel where the incoming events (Comments and messages) are registered in our platform and processed as tickets.  Some select users of the platform can then send replies to these tickets which are then shown as replies in Instagram.",
  privacypolicy32: "3. Information Processing",
  privacypolicy33: "Laraigo is a conversation management platform. It aims to centralize multiple social channels into one website from which you and your partners will be able to reply to most of the incoming social events. The information collected and mentioned on the point 2 of this document is processed and used to feed the message inbox of your user or or your company's users. The comments, messages and posts made on your linked social channels will be shown as tickets, which you will be able to interact along with other things like bot automatization and real agent transferring.",
  privacypolicy34: "4. Security",
  privacypolicy35: "The security of your personal and business information is important to us. We don’t share or rent your information with third parties. The information submitted will only be available in our platform for you and the users you choose.",
  privacypolicy36: "5. Deletion Request",
  privacypolicy37: "We are open to any data deletion request if needed. To start the process, please contact us to the following e-mail: laraigo@vcaperu.com. Please, be clear in your request and provide the credentials that you have used in our sign-up process in order to identify the data that will be deleted. This process should take from 1 to 2 days.",
  privacypolicy38: "6. Changes to This Privacy Policy",
  privacypolicy39: "This Privacy Policy is effective as of 2021-11-17 and will remain in effect except with respect to any changes in its provisions in the future, which will be in effect immediately after being posted on this page.",
  privacypolicy40: "We reserve the right to update or change our Privacy Policy as any time and you should check this Privacy Policy periodically.",
  privacypolicy41: "If we make any changes to this Privacy Policy, we will notify either through the e-mail address you have provided us, or by placing a prominent notice in our website.",
  privacypolicy42: "7. Contact Us",
  privacypolicy43: "If you have any questions about this Privacy Policy, please contact us by using this e-mail: laraigo@vcaperu.com",
